/*---
title: Icons
section: Components
---*/

:root {
  --icon-size-small: 17px;
  --icon-size-normal: 24px;
  --icon-size-medium: 35px;
  --icon-size-large: 48px;
  --icon-size-xlarge: 64px;
  --icon-size-huge: 128px;
}

.c-icon {
  display: inline-block;
  fill: currentColor;
  height: var(--icon-size-normal);
  user-select: none;
  vertical-align: middle;
  width: var(--icon-size-normal);

  svg {
    height: inherit;
    width: inherit;
  }

  &.is-passive { color: var(--grey-300); }

  &.is-disabled { opacity: .35; }

  &.is-active { color: var(--blue-600); }
}

.c-icon--small {
  height: var(--icon-size-small) !important;
  width: var(--icon-size-small) !important;
}

.c-icon--medium {
  height: var(--icon-size-medium) !important;
  width: var(--icon-size-medium) !important;
}

.c-icon--large {
  height: var(--icon-size-large) !important;
  width: var(--icon-size-large) !important;
}

.c-icon--xlarge {
  height: var(--icon-size-xlarge) !important;
  width: var(--icon-size-xlarge) !important;
}

.c-icon--huge {
  height: var(--icon-size-huge) !important;
  width: var(--icon-size-huge) !important;
}
