/* ---
title: Editor
section: Objects
---*/

@import 'stylep-card';

.o-editor {
  @mixin card-block;
  @mixin card-solid var(--white), #444, none;
  border: 1px solid rgba(255, 255, 255, 0);
  border-radius: var(--global-rounded);
  transition: border .1s ease;
  overflow: visible;

  @media (--md-up) {
    overflow: hidden;

    &:hover {
      border: 1px solid var(--blue-200);

      & > .o-editor__toolbar .o-editor__actions {
        @mixin fade-in;
      }
    }
  }

  &:focus {
    outline: 0;

    & > .o-editor__toolbar .o-editor__actions {
      @mixin fade-in;
    }
  }

  &:focus,
  &.is-active {
    border: 1px solid var(--blue-200);
  }

  & .o-editor__done { display: none; }

  &.is-active {

    & > .o-editor__toolbar,
    & > .o-editor__toolbar .o-editor__actions,
    & > .o-editor__content .o-editor__conditionals,
    & > .o-editor__toolbar .o-editor__nav,
    & > .o-editor__toolbar .o-editor__extras {
      @mixin fade-in;
    }

    & > .o-editor__content .o-editor__conditionals {
      height: auto;
      margin-bottom: 1em;
    }

    & > .o-editor__toolbar {
      height: 36px;
    }

    & > .o-editor__toolbar--small {
      height: 24px;
    }

    & > .o-editor__toolbar .o-editor__done { display: inline-block; }

    & > .o-editor__toolbar .o-editor__edit { display: none; }
  }

  & & {
    margin-bottom: 3em;

    @media (--md-up) {
      margin-bottom: 1em;
    }
  }
}

.o-editor__toolbar {
  @mixin row;
  background-color: var(--blue-200);
  height: 0;

  /* TODO Needs classes */

  select {
    display: inline-block;
    height: 30px;
    margin: 0 0 3px;
    max-width: 50%;
  }

  label {
    display: inline-block;
    margin: 5px 10px 0 0;
  }
}

.o-editor__nav,
.o-editor__extras,
.o-editor__toolbar--small {
  @mixin fade-out;
}

.o-editor__nav,
.o-editor__info {
  @mixin column-pattern;
  padding: 0;
}

.o-editor__info {
  @mixin end;
}

.o-editor__extras {
  display: inline-block;
}

.o-editor__toolbar--small {
  @mixin middle;
  padding: 0 5px;

  .c-button {
    height: 25px;
    line-height: 25px;
    padding: 0;
    margin: 0;
  }

  .c-checkbox + label {
    margin: 0;
  }
}

.o-editor__actions {
  @mixin fade-out;
  @mixin center;
  background-color: var(--blue-800);
  margin: 0 -1px;
  position: absolute;
  left: 0;
  right: 0;
  top: 100%;
  @media (--md-up) {
    @mixin start;
    background-color: transparent;
    margin: 0;
    padding-top: 0;
    position: static;
  }

  .c-button {
    border-radius: 0;
  }

  .o-editor:not(.is-active) & .o-editor__done,
  .o-editor:not(.is-active) & .o-editor__edit {
    border-bottom-left-radius: var(--global-rounded) !important;
    border-top-left-radius: 0;
  }
}

.o-editor__more-action {
  margin-right: .5rem;
}

.o-editor__content {
  @mixin start;
  padding: .75em 1rem;
}

.o-editor__conditionals {
  @mixin row;
  @mixin middle;
  @mixin fade-out;
  height: 0;
}


.c-button--favorite {
  @mixin button-solid transparent, transparent, var(--blue-700), none, var(--button-round);
  font-size: var(--font-size--small) !important;
  margin: 0 6px 0 0 !important;
  padding: 8px 0 !important;
  position: relative;
  text-transform: uppercase;

  .c-icon {
    bottom: 1.5px;
    color: var(--blue-700);
    height: 23px;
    position: relative;
    transition: color .1s ease-in-out;
    width: 23px;
  }

  &::before {
    background-color: transparent;
    border-radius: 50%;
    box-shadow: 0 0 0 rgba(0, 0, 0, 0);
    content: '';
    display: inline-block;
    height: 8px;
    left: 8px;
    position: absolute;
    top: 16px;
    transition: box-shadow .1s ease;
    width: 8px;
  }

  &.is-active {
    background-color: transparent;
    box-shadow: none;

    &::before {
      box-shadow: 0 0 10px rgba(0, 0, 0, .72), 0 0 10px rgba(0, 0, 0, .72);
    }

    .c-icon { color: var(--gold-200); }
  }
}
