/*---
title: Menu
section: Components
---*/

:root {
  --menu-active-color: var(--nav-active-color);
  --menu-active-copy: var(--nav-active-copy);
  --menu-item-height: 41px;
}

.c-menu {
  @mixin list-horizontal;
  @mixin no-bullets;
  @mixin row;
  background-color: transparent;
  color: currentColor;
  position: relative;
  text-decoration: none;
  user-select: none;
  vertical-align: middle;
}

.c-menu__link {
  background-color: transparent;
  color: currentColor;
  cursor: pointer;
  font-family: inherit;
  padding: 0 .5rem;
  @media (--md-up) {
    min-width: 100px;
  }

  &:focus { outline: 0; }

  &.is-active {
    background-color: var(--blue-800);
    box-shadow: inset 0 0 6px rgba(0, 0, 0, .8);
    color: var(--menu-active-copy);
  }
}

.c-menu__link--small {
  font-size: var(--font-size--small);
  height: 38px;
  line-height: 38px;
}

.c-menu--block {
  margin: 0 -.5rem;

  .c-menu__link {
    display: block;
    font-size: var(--font-size--tiny);
    height: var(--menu-item-height);
    line-height: var(--menu-item-height);
    padding: 0 12px;
    transition: background-color var(--global-duration) ease, color var(--global-duration) ease;
    white-space: nowrap;
    width: 100%;
    @media (--md-up) {
      font-size: var(--font-size--normal);
      padding: 0 24px;
    }

    &:hover,
    &:active,
    &.is-active {
      background-color: var(--menu-active-color);
      box-shadow: none;
      color: var(--menu-active-copy);
    }

    &.is-active + .c-menu__submenu {
      @mixin fade-in;
    }
  }

  .c-menu__submenu {
    @mixin fade-out;
    @mixin no-bullets;
    @mixin reset-box;
    color: currentColor;
    display: block;
    min-width: 150px;

    &.is-active {
      @mixin fade-in;
    }
  }
}

.c-menu--inline {

  .c-menu__link {
    border-bottom: 3px solid transparent;
    font-size: var(--font-size--tiny);
    letter-spacing: 1px;
    min-height: 36px;
    padding: .5em .5rem;
    text-transform: uppercase;
    transition: border-bottom var(--global-duration) ease, color var(--global-duration) ease;

    @media (--md-up) {
      min-width: auto;
      padding: .5em .75rem;
    }

    &:hover { border-bottom-color: var(--blue-400); }

    &:active,
    &.is-active {
      background-color: transparent;
      border-bottom-color: var(--blue-800);
      box-shadow: none;
      color: var(--blue-800);
    }
  }
}
