/*---
title: Tooltips
section: Components
---*/

:root {
  --tooltip-arrow-size: 10px;
  --tooltip-background: var(--white);
  --tooltip-space: .5rem;
  --tooltip-transform: translateX(-50%);
  --tooltip-z-index: 50;
}

@define-mixin svg-arrow $color, $position: up {
  @if $position == up {
    background: no-repeat url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" width="32" height="12"><path fill="$(color)" transform="rotate(0)" d="M2.658,0.000 C-13.615,0.000 50.938,0.000 34.662,0.000 C28.662,0.000 23.035,12.002 18.660,12.002 C14.285,12.002 8.594,0.000 2.658,0.000 Z"/></svg>');
    height: 6px;
    width: 18px;
  } @else if $position == down {
    background: no-repeat url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" width="32" height="12"><path fill="$(color)" transform="rotate(180 18 6)" d="M2.658,0.000 C-13.615,0.000 50.938,0.000 34.662,0.000 C28.662,0.000 23.035,12.002 18.660,12.002 C14.285,12.002 8.594,0.000 2.658,0.000 Z"/></svg>');
    height: 6px;
    width: 18px;
  } @else if $position == left {
    background: no-repeat url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" width="12" height="32"><path fill="$(color)" transform="rotate(-90 18 18)" d="M2.658,0.000 C-13.615,0.000 50.938,0.000 34.662,0.000 C28.662,0.000 23.035,12.002 18.660,12.002 C14.285,12.002 8.594,0.000 2.658,0.000 Z"/></svg>');
    height: 18px;
    width: 6px;
  } @else if $position == right {
    background: no-repeat url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" width="12" height="32"><path fill="$(color)" transform="rotate(90 6 6)" d="M2.658,0.000 C-13.615,0.000 50.938,0.000 34.662,0.000 C28.662,0.000 23.035,12.002 18.660,12.002 C14.285,12.002 8.594,0.000 2.658,0.000 Z"/></svg>');
    height: 18px;
    width: 6px;
  }
  background-size: 100% auto;
}

[data-tooltip] {
  position: relative;

  &::after,
  &::before {
    bottom: 100%;
    left: 50%;
    opacity: 0;
    pointer-events: none;
    position: absolute;
    transform: translate(-50%, 10px);
    transform-origin: top;
    transition: all .18s ease-out .18s;
    z-index: 10;
  }

  &::after {
    background: var(--tooltip-background);
    border-radius: var(--global-rounded);
    box-shadow: 0 0 7px 3px rgba(0, 0, 0, .2);
    color: var(--body-copy);
    content: attr(data-tooltip);
    font-size: var(--font-size--small);
    line-height: 1;
    margin-bottom: calc(1px + var(--tooltip-arrow-size));
    padding: .5em 1rem;
    right: auto;
    top: auto;
    white-space: nowrap;
  }

  &::before {
    @mixin svg-arrow var(--tooltip-background);
    content: '';
    margin-bottom: 5px;
    z-index: 40;
  }

  &:hover {
    &::after,
    &::before {
      opacity: 1;
      pointer-events: auto;
      transform: translate(-50%, 0);
    }
  }

  &[data-tooltip-break] {
    &::after { white-space: normal; }
  }
}

[data-tooltip-pos="down"] {
  &::after,
  &::before {
    bottom: auto;
    left: 50%;
    top: 100%;
    transform: translate(-50%, -10px);
  }

  &::after { margin-top: calc(1px + var(--tooltip-arrow-size)); }

  &::before {
    @mixin svg-arrow var(--tooltip-background), 'down';
    margin-top: 5px;
    margin-bottom: 0;
  }

  &:hover {
    &::after,
    &::before {
      transform: translate(-50%, 0);
    }
  }
}

[data-tooltip-pos="left"] {
  &::after,
  &::before {
    bottom: auto;
    left: auto;
    right: 100%;
    top: 50%;
    transform: translate(10px, -50%);
  }

  &::after {
    margin-right: calc(1px + var(--tooltip-arrow-size));
  }

  &::before {
    @mixin svg-arrow var(--tooltip-background), 'left';
    margin-right: 5px;
    margin-bottom: 0;
  }

  &:hover {
    &::after,
    &::before {
      transform: translate(0, -50%);
    }
  }
}

[data-tooltip-pos="right"] {
  &::after,
  &::before {
    bottom: auto;
    left: 100%;
    top: 50%;
    transform: translate(-10px, -50%);
  }

  &::after {
    margin-left: calc(1px + var(--tooltip-arrow-size));
  }

  &::before {
    @mixin svg-arrow var(--tooltip-background), 'right';
    margin-bottom: 0;
    margin-left: 5px;
  }

  &:hover {
    &::after,
    &::before {
      transform: translate(0, -50%);
    }
  }
}

[data-tooltip-length="small"] {
  &::after {
    white-space: normal;
    width: 80px;
  }
}

[data-tooltip-length="medium"] {
  &::after {
    white-space: normal;
    width: 150px;
  }
}

[data-tooltip-length="large"] {
  &::after {
    white-space: normal;
    width: 260px;
  }
}

[data-tooltip-length="xlarge"] {
  &::after {
    white-space: normal;
    width: 380px;

    @media (--sm-down) {
      white-space: normal;
      width: 90vw;
    }
  }
}

[data-tooltip-length="fit"] {
  &::after {
    white-space: normal;
    width: 100%;
  }
}
