/* Links */

@define-mixin focus {
  &:focus { @mixin-content; }

  @media (--lg-up) {
    &:hover { @mixin-content; }
  }
}

@define-mixin active {
  &:active,
  &.is-active { @mixin-content; }
}

@define-mixin disabled {
  &[disabled],
  &.is-disabled { @mixin-content; }
}



@define-mixin link-colors $normal, $hover, $active, $visited, $focus {
  color: $normal;
  &:visited { color: $visited; }
  &:focus { color: $focus; }
  &:hover { color: $hover; }
  &:active { color: $active; }
}

@define-mixin responsive-link-colors $color, $hover, $active {
  color: $color;
  &:hover { color: $hover; }
  @mixin active { color: $active; }
}

@define-mixin link-hover {
  text-decoration: none;

  &:hover,
  &:focus { text-decoration: underline; }
}

@define-mixin link-unstyled {
  color: inherit;
  cursor: inherit;
  text-decoration: inherit;

  &:active,
  &:focus { outline: none; }
}

/* Overflow */

@define-mixin ellipsis {
  display: inline-block;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  width: 100%;
}

@define-mixin break-word {
  /* Safari can wait */
  -ms-word-break: break-all;
  word-break: break-word;
  word-wrap: break-word;
}
/* Typography classes */

.u-no-bullet { @mixin no-bullet; }
.u-no-bullets { @mixin no-bullets; }
.u-list-horizontal { @mixin list-horizontal; }
.u-ellipsis { @mixin ellipsis; }
.u-break-word { @mixin break-word; }